<template>
  <div class="homepage">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>
    <section>
      <van-grid>
        <van-grid-item
          v-for="item in menuList"
          :key="item"
          icon="data:image/png;base64,"
          :text="item.title"
          @click="goTo(item.url)"
        />
      </van-grid>
      <van-image :src="img" />
    </section>
  </div>
</template>
<script>
import { Swipe, SwipeItem, Grid, GridItem } from "vant";
export default {
  name: "homePage",
  components: {
    [Grid.name]: Grid,
    [GridItem.name]: GridItem,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  data: () => ({
    menuList: [
      {
        title: "登录",
        icon: "",
        url: "/login",
      },
      {
        title: "注册",
        icon: "",
        url: "/registry",
      },
      {
        title: "天气预报",
        icon: "",
        url: "https://www.msn.cn/zh-cn/weather/forecast",
      },
      {
        title: "天气预报",
        icon: "",
        url: "",
      },
      {
        title: "天气预报",
        icon: "",
        url: "",
      },
      {
        title: "天气预报",
        icon: "",
        url: "",
      },
      {
        title: "天气预报",
        icon: "",
        url: "",
      },
      {
        title: "天气预报",
        icon: "",
        url: "",
      },
    ],
    img: require("./../../assets/logo.png"),
  }),
  created() {},
  methods: {
    goTo(url) {
      if (url && /^http(s)?:\/\//.test(url)) {
        window.location.href = url;
        return;
      }
      this.$router.push(url);
    },
  },
};
</script>
